import React from 'react';

interface Props {}

const HelpDialog: React.FC<Props> = () => (
  <div >
    <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#exampleModalScrollable">
      Hilfe
    </button>

    <div className="modal fade" id="exampleModalScrollable" role="dialog" aria-labelledby="exampleModalScrollableTitle" aria-hidden="true">
      <div className="modal-dialog modal-dialog-scrollable modal-lg" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="exampleModalScrollableTitle">Hilfe</h5>
            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <h4>berblick</h4>
            <p>
              Dieses Werkzeug dient zur Aufteilung einer Klassenstufe in mehrere Gruppen, die im Wechsel unterrichtet
              werden. Es bercksichtigt insbesondere Klassenstufen, in denen der Unterricht nicht durchgngig im 
              Klassenverbund durchgefhrt wird. Es kann also je Schler eine individuelle Kursbelegung geben.
            </p>
            <p>
              Lsungen werden heuristisch berechnet. Somit wird nicht unbedingt die optimale Lsung gefunden. 
              Hauptbedingung fr eine gute Lsung ist eine mglichst geringe Anzahl an paarweisen Kontakten von Schlern.
              Ein Kontakt entseht dabei, wenn zwei Schler gemeinsam in einem Kurs sind. Zustzlich knnen Beschrnkungen
              fr die resultierende Kursgre bercksichtigt werden.
            </p>
            <h4>Eingabe Format</h4>
            <p> 
              Als Eingabe wird eine CSV Datei erwartet. Die erste Zeile der Datei wird ignoriert (Kopfzeile).
              Alle weiteren Zeilen entsprechen einem Schler. Die 
              ersten beiden Werte werden als Nachname und Vorname des Schlers verstanden. Alle weiteren Werte werden als Namen der 
              vom Schler belegten Kurse interpretiert. Die Kursnamen mssen eindeutig sein, auch in der Schreibweise.  Leere Werte 
              sind zulssig und werden ignoriert. 
            </p>
            <h4>Bedienung</h4>
            <p>
              Zunchst ist im Bereich <i>Eingabe</i> eine Datei im erwarteten Format zu laden. Im Bereich <i>Berechnen</i> kann bei 
              Bedarf die <i>Konfiguartion</i> angepasst werden (Details s.u.) und dann die <i>Lsung berechnet</i> werden. Bei der 
              Berechnung wird der Lsungsalgorithmus mehrmals durchlaufen und die beste gefundene Lsung ausgegeben. Die Anzahl
              der <i>Durchlufe</i> kann vorgegeben werden.
            </p>
            <p>
              Die Ausgabe der eingelesenen und berechneten Daten erfolgt im unteren Bereich tabellarisch und kann als CSV-Datei 
              gesichert werden. Die Tabelle enthlt folgende Spalten:
            </p>
            <dl>
              <dt>no</dt>
              <dd>Nummer des Schlers</dd>
              <dt>lastname</dt>
              <dd>Nachname des Schlers</dd>
              <dt>firstname</dt>
              <dd>Vorname des Schlers</dd>
              <dt>combi</dt>
              <dd>
                Eine numerische ID fr die Kombination von belegten Kursen des Schlers. Genau die Schler mit der identischen 
                Kursbelegung haben hier die selbe ID.
              </dd>
              <dt>group</dt>
              <dd>
                ID der Gruppe, der der Schler bei der Teilung zugeordnet wurde. Die IDs werden numerisch vergeben beginnend  mit Null (0).
              </dd>
              <dt>block-n</dt>
              <dd>
                Name des Kurses den der Schler im n-ten Block belegt. Die Aufteilung der Kurse auf Blcke werden automatisch berechnet und 
                mssen somit nicht den echten zeitlichen Blcken im Stundenplan entsprechen.  
              </dd>
            </dl>
            <p>
              <b><i>Hinweis:</i></b> Schler in mit der selben Kursbelegung (combi) sind gleichwertig. Sollten Schler aus einer Kursbelegung 
              auf unterschiedliche Gruppen verteilt worden sein, so ist fr das Ergebnis irrelevant welcher konkrete Schler in welcher
              Gruppe ist. Es kann also innerhalb einer Kursbelegung getauscht werden, so lange die Anzahl der Schler je Gruppe nicht
              verndert wird.
            </p>
            <h4>Konfiguration</h4>
            <p>
              Optionen zur <i>Konfiguration</i> knnen auf Wunsch eingeblendet werden. Die Konfigurationen teilen sich in zwei Bereiche.
              Unterhalb der Einstellungen existieren Buttons zur Einzeldurchfhrung der Optimierung und zur schrittweisen Ausfhrung.
            </p>
            <h5>Grupen Einstellungen</h5>
            <dl>
              <dt>Anzahl Gruppen</dt>
              <dd>Legt fest in wieviele Gruppen der Jahrgang aufgeteilt werden soll. Mglich sind 2 bis 5 Gruppen.</dd>
              <dt>Begrenzung Gruppen Gre.</dt>
              <dd>
                Definiert die maximale Gruppen Gre. Optionen sind:
                <ul>
                  <li><b>Keine Begrenzung</b> - Die Gre der resultierenden Kurse ist unbegrenzt</li>
                  <li
                    ><b>anhand grtem Kurs</b> - Die Gre der resultierenden Kurse ist begrenzt durch die Gre des 
                    grten Kurses geteilt durch die Anzahl Gruppen.
                  </li>
                  <li>
                    <b>je Kurs</b> - Die Gre der resultierenden Kurse ist begrenzt durch die Gre des 
                    jeweilgen Kurses geteilt durch die Anzahl Gruppen.
                  </li>
                </ul>
              </dd>
              <dt>Toleranz Gruppen Gre</dt>
              <dd>
                Bei einer Begrenzung der Gruppen Gre drfen die Gren der resultierenden Kurse die Begrenzung um diese
                Anzahl an Schlern bersteigen.
              </dd>
            </dl>
            <h5>Parameter des Algorithmus</h5>
            <dl>
              <dt>Initialisierung</dt>
              <dd>
                Legt die initiale Verteilung der Schler auf Gruppen fest. Entweder werden initial alle Schler der 
                ersten Gruppe zugeordnet oder die Verteilung erfolgt zufllig gleichverteilt auf alle Gruppen.
              </dd>
              <dt>Optimierung</dt>
              <dd>
                Legt fest ob der Algorithmus sich bevorzugt langsam oder schnell dem Ziel nhert. Abhngig von dieser Einstellung 
                wird in jeder Runde entweder der Schritt mit der kleinsten oder der grten Verbesserung zur Ausfhrung gewhlt.
              </dd>
              <dt>Schrittauswahl</dt>
              <dd>
                Bei deterministischer Auswahl wird der erste gefunden Schritt mit der kleinsten / grten Verbesserung ausgefhrt.
                Bei zuflliger Auswahl wird aus den kleinsten / grten Schritten zufllig ein Schritt zur Ausfhrung ausgewhlt.
              </dd>
              <dt>Schritt Auswahl aus X %</dt>
              <dd>
                Legt fest, wieviel Prozent aller mglichen Schritte bei der zuflligen Auswahl bercksichtigt werden.
              </dd>
            </dl>
            <h4>Algorithmus</h4>
            <p>
              Zur Initialisierung des Algorithmus wrden die Schler auf Gruppen verteilt, entweder zufllig oder alle in die erste Gruppe.
              Der Optimierungsalgorithmus arbeitet in zwei Phasen. In der ersten Phase wird die Verteilung so angepasst, dass die 
              angestrebten Gren der Teilkurse eingehalten werden. In der zweiten Phase wird die Verteilung so verndert, dass die 
              Anzahl der Kontakte mglichst gering wird.
            </p>
            <p>
              Beide Phasen werden in Schritten durchgefhrt. Ein Schritt ist dabei die Verschiebung eines Schlers von einer Gruppe in 
              eine andere Gruppe. Zur Festlegung eines Schrittes werden zunchst alle mglichen Schritte ermittelt, die zur Verbesserung 
              des aktuellen Phasenziels beitragen. Gibt es keine solchen Schritte ist die Phase abgeschlossen. Die gefundenen mglichen 
              Schritte werden gewichtet anhand der Verndeung an der Anzahl der Kontakte, die der jeweilge Schritt bewirken wird. Aus den
              nach Gewicht sortierten Schritten wird der auszufhrende Schritt gem dern gewhlten Optioenn ausgewhlt und ausgefhrt. 
            </p>
          </div> 
          <div className="modal-footer">
            <button type="button" className="btn btn-secondary" data-dismiss="modal">Schliessen</button>
          </div>
        </div>
      </div>
    </div>
</div>
);

export default HelpDialog;